.recalls {
  padding: 90px 0 97px;
  &__title {
    margin-bottom: 25px; }
  &__block {
    &s {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between; }
    margin-top: 60px;
    margin-right: auto;
    margin-left: auto;
    max-width: 400px;
    font-family: $font-regular;
    font-size: 16px;
    line-height: 22px; }
  &__text {
    position: relative;
    padding: 30px 20px;
    text-align: left;
    background-color: $lighten-color;
    &::after {
      content: '';
      position: absolute;
      right: 0;
      bottom: -20px;
      border: 20px solid transparent;
      border-top: 0px solid transparent;
      border-left: 0px solid transparent;
      border-right: 30px solid $lighten-color; } }
  &__caption {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 22px;
    line-height: 20px; }
  &__name {
    margin-right: 10px;
    font-family: $font-bold;
    text-align: right; }
  &__profesion {
    display: block;
    font-family: $font-regular; } }

.slider-dots {
  width: 90%;
  margin: 0 auto;
  margin-top: 20px;
  display: flex;
  justify-content: center;
  .dot {
    display: none;
    cursor: pointer;
    height: 16px;
    width: 16px;
    margin: 0 10px;
    background-color: $bright-color;
    border-radius: 50%;
    transition: background-color 0.6s ease;
    &-active {
      background-color: $dark-color;
      height: 18px;
      width: 18px; } } }

@media (max-width: 1440px) {
  .recalls {
    &__title {
      margin-bottom: 0; }
    &__block {
      max-width: 300px; } } }

@media (max-width: 992px) {
  .recalls {
    padding: 80px 0; } }

@media (max-width: 768px) {
  .recalls {
    padding: 50px 0;
    &__block {
      margin-top: 30px; } } }

