.main {
  padding: 220px 0 158px;
  text-align: center;
  color: $white;
  // background: $dark-color url(../img/main-slider/item-1.jpg) center/cover no-repeat
  // background-image: url(../img/main-slider/item-1.jpg)
  // background-color: $dark-color
  position: relative;
  z-index: 1;
  &::before {
    content: '';
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-color: rgba($dark-color, 0.6);
    z-index: -1; }
  &__slider {
    // display: none
    &__item {
      display: none;
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: 100%;
      z-index: -1;
      // transition: display 0.2s ease 0s
      &:first-child {
        display: block; }
      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        object-position: top; } } }
  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba($dark-color, 0.6);
    z-index: -1; }
  &__title {
    margin: 0;
    font-family: $font-black;
    font-size: 80px;
    color: $light-color; }
  &__subtitle {
    margin-top: 10px;
    font-family: $font-black;
    font-size: 58px; }
  &__description {
    margin: 35px auto 50px;
    width: 800px;
    font-family: $font-regular;
    font-size: 26px;
    &-mark {
      font-family: $font-bold;
      color: $light-color; } } }

@media (max-width: 992px) {
  .main {
    padding: 100px 0;
    &__description {
      width: 100%; } } }
@media (max-width: 768px) {
  .main {
    padding: 75px 0 50px;
    &__title {
      font-size: 54px; }
    &__subtitle {
      font-size: 36px; }
    &__description {
      font-size: 20px; } } }
